# Base
--------------------------------------------------------------*/
/* Typography
--------------------------------------------- */
body,
button,
input,
select,
optgroup,
textarea {
  font-size: 1.125rem;
  line-height: 1.5;
}

h1, .h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6 {
  clear: both;
  margin: 0 0 1.25rem;
  line-height: 1.2;
}

h1, .h1 {
  font-size: 1.75rem;
}

h2, .h2 {
  font-size: 1.5625rem;
}

h3, .h3 {
  font-size: 1.4375rem;
}

h4, .h4 {
  font-size: 1.3125rem;
}

h5, .h5 {
  font-size: 1.25rem;
}

h6, .h6 {
  font-size: 1rem;
}

@media (min-width: 767px) {
  h1, .h1 {
    font-size: 2.75rem;
  }

  h2, .h2 {
    font-size: 2.25rem;
  }

  h3, .h3 {
    font-size: 2rem;
  }

  h4, .h4 {
    font-size: 1.75rem;
  }

  h5, .h5 {
    font-size: 1.5rem;
  }

  h6, .h6 {
    font-size: 1.25rem;
  }
}
p {
  margin: 0 0 1em;
}

dfn,
cite,
em,
i {
  font-style: italic;
}

blockquote {
  border-left: 5px solid rgba(0, 0, 0, 0.05);
  padding: 20px;
  font-size: 1.2em;
  font-style: italic;
  margin: 0 0 1.5em;
  position: relative;
}
blockquote p:last-child {
  margin: 0;
}

address {
  margin: 0 0 1.5em;
}

pre {
  background: #eee;
  font-family: "Courier 10 Pitch", courier, monospace;
  line-height: 1.6;
  margin-bottom: 1.6em;
  max-width: 100%;
  overflow: auto;
  padding: 1.6em;
}

code,
kbd,
tt,
var {
  font-family: monaco, consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
}

abbr,
acronym {
  border-bottom: 1px dotted #666;
  cursor: help;
}

mark, .mark,
ins {
  background: #fff9c0;
  text-decoration: none;
}

big {
  font-size: 125%;
}

/* Elements
--------------------------------------------- */
html {
  scroll-behavior: smooth;
}

body {
  background: #fff;
}

hr {
  background-color: #ccc;
  border: 0;
  height: 1px;
  margin-bottom: 40px;
  margin-top: 40px;
}

ul,
ol {
  margin-top: 1.5em;
  margin-bottom: 1.5em;
  padding-left: 1.5em;
}

ul {
  list-style: disc;
}

ol {
  list-style: decimal;
}

li > ul,
li > ol {
  margin-bottom: 0;
  margin-left: 1.5em;
}

dt {
  font-weight: 700;
}

dd {
  margin: 0 1.5em 1.5em;
}

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
  max-width: 100%;
}

img {
  height: auto;
  max-width: 100%;
}

figure {
  margin: 1em 0;
}

table,
td,
th {
  border: 1px solid rgba(0, 0, 0, 0.1);
}

table {
  border-collapse: separate;
  border-spacing: 0;
  border-width: 1px 0 0 1px;
  margin: 0 0 1.5em;
  width: 100%;
}

td,
th {
  padding: 8px;
}

th {
  border-width: 0 1px 1px 0;
}

td {
  border-width: 0 1px 1px 0;
}

.svg-icon {
  display: inline-block;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
  position: relative;
  top: -1px;
  vertical-align: middle;
}

/* Links
--------------------------------------------- */
a {
  color: #4169e1;
  text-decoration: none;
}
a:hover, a:focus, a:active {
  color: #191970;
}
a:focus {
  outline: thin dotted;
}
a:hover, a:active {
  outline: 0;
}

/* Forms
--------------------------------------------- */
.button,
button,
input[type=button],
input[type=reset],
input[type=submit] {
  display: inline-block;
  border: 1px solid;
  border-radius: 25px;
  line-height: 1.5;
  cursor: pointer;
  padding: 0.625rem 1.5625rem;
  transition: all 300ms ease-in-out;
  font-size: 1rem;
  font-weight: 600;
  position: relative;
  transform: translateY(0);
}
.button:hover, .button:active, .button:focus,
button:hover,
button:active,
button:focus,
input[type=button]:hover,
input[type=button]:active,
input[type=button]:focus,
input[type=reset]:hover,
input[type=reset]:active,
input[type=reset]:focus,
input[type=submit]:hover,
input[type=submit]:active,
input[type=submit]:focus {
  transform: translateY(-2px);
  filter: brightness(85%);
}

.button--block {
  display: block;
  text-align: center;
}
.button--lg {
  padding: 0.625rem 1.875rem;
  font-size: 1rem;
}

input[type=text],
input[type=email],
input[type=url],
input[type=password],
input[type=search],
input[type=number],
input[type=tel],
input[type=range],
input[type=date],
input[type=month],
input[type=week],
input[type=time],
input[type=datetime],
input[type=datetime-local],
input[type=color],
select,
textarea {
  color: #666;
  border: 1px solid #ccc;
  padding: 0.625rem 1.25rem;
  width: 100%;
  display: block;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  appearance: none;
}
input[type=text]:focus,
input[type=email]:focus,
input[type=url]:focus,
input[type=password]:focus,
input[type=search]:focus,
input[type=number]:focus,
input[type=tel]:focus,
input[type=range]:focus,
input[type=date]:focus,
input[type=month]:focus,
input[type=week]:focus,
input[type=time]:focus,
input[type=datetime]:focus,
input[type=datetime-local]:focus,
input[type=color]:focus,
select:focus,
textarea:focus {
  color: #111;
}

fieldset {
  padding: 0;
  border: initial;
}

.ftf-select {
  background-color: #fff;
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3e%3cpath fill='none' stroke='%23343a40' stroke-linecap='round' stroke-linejoin='round' stroke-width='2' d='M2 5l6 6 6-6'/%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-position: right 0.75rem center;
  background-size: 16px 12px;
  appearance: none;
  padding-right: 40px;
}

