/*--------------------------------------------------------------
# Layouts
--------------------------------------------------------------*/
.content-sidebar-wrap {
  flex-grow: 1;
  width: 100%;
}

.fivetwofive-page-layout-contained .content-sidebar-wrap {
  margin-left: auto;
  margin-right: auto;
  padding: 0.9375rem;
}

@media (min-width: 576px) {
  .fivetwofive-page-layout-contained .content-sidebar-wrap {
    max-width: 540px;
  }
}
@media (min-width: 768px) {
  .fivetwofive-page-layout-two-column .content-sidebar-wrap,
.fivetwofive-page-layout-contained .content-sidebar-wrap {
    padding-top: 3.125rem;
    padding-bottom: 3.125rem;
  }

  .fivetwofive-page-layout-contained .content-sidebar-wrap {
    max-width: 720px;
  }

  .fivetwofive-page-layout-two-column .content-sidebar-wrap {
    display: flex;
  }
  .fivetwofive-page-layout-two-column .site-main,
.fivetwofive-page-layout-two-column .site-sidebar {
    padding-left: 0.9375rem;
    padding-right: 0.9375rem;
  }
  .fivetwofive-page-layout-two-column .site-main {
    flex-basis: 70%;
    width: 70%;
    margin-left: -0.9375rem;
  }
  .fivetwofive-page-layout-two-column .site-sidebar {
    flex-basis: 30%;
    width: 30%;
    margin-right: -0.9375rem;
  }
}
@media (min-width: 992px) {
  .fivetwofive-page-layout-contained .content-sidebar-wrap {
    max-width: 960px;
  }
}
@media (min-width: 1200px) {
  .fivetwofive-page-layout-contained .content-sidebar-wrap {
    max-width: 1140px;
  }
}
@media (min-width: 1400px) {
  .fivetwofive-page-layout-contained .content-sidebar-wrap {
    max-width: 1320px;
  }
}
.site {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/*--------------------------------------------------------------
# Templates
--------------------------------------------------------------*/
.error404 .content-sidebar-wrap {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

/*--------------------------------------------------------------
# Archive
--------------------------------------------------------------*/
.blog,
.archive {
  overflow-x: hidden;
}
.blog .content-sidebar-wrap,
.archive .content-sidebar-wrap {
  display: block;
  padding-top: 0;
  padding-bottom: 50px;
}
.blog .card__image,
.archive .card__image {
  height: 250px;
  object-fit: cover;
}

.view-toggle {
