/* Spinner
--------------------------------------------- */
.fivetwofive-spinner {
  display: inline-block;
  position: relative;
  width: 5em;
  height: 5em;
  font-size: 1rem;
}
.fivetwofive-spinner div {
  position: absolute;
  border: 4px solid #444444;
  opacity: 1;
  border-radius: 50%;
  -webkit-animation: fivetwofive-spinner 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
  animation: fivetwofive-spinner 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.fivetwofive-spinner div:nth-child(2) {
  -webkit-animation-delay: -0.5s;
  animation-delay: -0.5s;
}

@-webkit-keyframes fivetwofive-spinner {
  0% {
    top: 2.25em;
    left: 2.25em;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: 0px;
    left: 0px;
    width: 4.5em;
    height: 4.5em;
    opacity: 0;
  }
}
@keyframes fivetwofive-spinner {
  0% {
    top: 2.25em;
    left: 2.25em;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: 0px;
    left: 0px;
    width: 4.5em;
    height: 4.5em;
    opacity: 0;
  }
}
/* Embed
--------------------------------------------- */
.embed-container {
  position: relative;
  padding-bottom: 56.25%;
  overflow: hidden;
  max-width: 100%;
  height: auto;
}
.embed-container iframe,
.embed-container object,
.embed-container embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

/* Avatar
--------------------------------------------- */
.ftf-avatar {
  display: flex;
  align-items: center;
}
.ftf-avatar__image {
  border-radius: 50%;
  display: block;
}
.ftf-avatar__image-col {
  margin-right: 1.25rem;
}
.ftf-avatar__text {
  font-size: 0.875rem;
  margin-bottom: 0.3125rem;
}
.ftf-avatar__name {
  font-size: 1.125rem;
  margin: 0;
}

/* Footer
--------------------------------------------- */
.site-footer {
  padding-top: 1.5625rem;
  padding-bottom: 1.5625rem;
}
.site-footer__bottom {
  padding-top: 1.5625rem;
  padding-bottom: 1.5625rem;
}
.site-footer__copyright {
  margin: 0;
}
.site-footer a,
.site-footer a:focus,
.site-footer a:visited {
  color: inherit;
}
.site-footer-menu {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  margin: 1.25rem 0 0;
  padding: 0;
}
@media only screen and (min-width: 768px) {
  .site-footer-menu {
    justify-content: flex-end;
    margin: 0;
  }
}
.site-footer-menu li:not(:last-child) {
  margin-right: 0.625rem;
  padding-right: 0.625rem;
  border-right: 1px solid;
}
.site-footer-menu a,
.site-footer-menu a:focus,
.site-footer-menu a:visited {
  color: inherit;
}

/* Skip Link
--------------------------------------------- */
.fivetwovive-skip-link {
  margin: 0;
}
.fivetwovive-skip-link > li {
  height: 0;
  list-style: none;
  width: 0;
}

.screen-reader-shortcut {
  border: 0;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  overflow: hidden;
  position: absolute !important;
  width: 1px;
  word-wrap: normal !important;
}
.screen-reader-shortcut:focus {
  background: #fff;
  -webkit-box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
  clip: auto !important;
  color: #333;
  display: block;
  font-size: 1em;
  font-weight: 700;
  height: auto;
  padding: 15px 23px 14px;
  text-decoration: none;
  width: auto;
  z-index: 100000;
}

/* Tooltips
--------------------------------------------- */
.tooltip {
  background: #292929;
  padding: 8px 10px;
  border-radius: 5px;
  display: none;
  margin-bottom: 5px;
}
.tooltip[data-show] {
  display: block;
}
.tooltip:after {
  content: "";
  width: 0;
  height: 0;
  border-width: 5px 5px 0 5px;
  border-color: #292929 transparent transparent transparent;
  border-style: solid;
  display: block;
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
}
.tooltip a {
  display: flex;
  align-items: center;
  font-size: 0.875rem;
  color: #fff !important;
  text-decoration: none;
}
.tooltip a svg {
  display: block;
  margin-right: 0.3125rem;
}
.tooltip a:hover {
  color: #fff;
}

/* Tag links
--------------------------------------------- */
.tags-links {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 5px;
}

/*--------------------------------------------------------------
# Utilities
--------------------------------------------------------------*/
/* Accessibility
--------------------------------------------- */
/* Text meant only for screen readers. */
.screen-reader-text {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute !important;
  width: 1px;
  word-wrap: normal !important;
}
.screen-reader-text:focus {
  background-color: #f1f1f1;
  border-radius: 3px;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
  clip: auto !important;
  clip-path: none;
  color: #21759b;
  display: block;
  font-size: 0.875rem;
  font-weight: 700;
  height: auto;
  left: 5px;
  line-height: normal;
  padding: 15px 23px 14px;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000;
}

/* Do not show the outline on the skip link target. */
#primary[tabindex="-1"]:focus {
  outline: 0;
}

